body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  max-width: 800px;
  margin: 30px auto 0 auto;
}

.header {
  cursor: pointer;
}

.line {
  fill: none;
  stroke: #e40000;
  stroke-width: 2px;
  stroke-linecap: round;
}

.topTechniquesSection {
  display: -webkit-flex;
  display: flex;
}

.winsSection, .lossesSection {
  width: 200px;
}

.winsSection {
  margin-right: 20px;
}

.totalWins, .totalLosses {
  font-weight: 700;
  font-size: 20px;
}

.totalWinsLabel, .totalLossesLabel {
  margin-right: 5px;
}

.topTechniques {
  margin-top: 10px;
}

.topTechnique {
  margin: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-size: 14px;
}

.topWinningTechniquesTitle, .topLoosingTechniquesTitle {
  font-size: 18px;
  border-bottom: 2px solid #000;
  letter-spacing: 1.5px;
}

.topOpponentsSection {
  display: -webkit-flex;
  display: flex;
}

.winsSection, .lossesSection {
  width: 200px;
}

.winsSection {
  margin-right: 20px;
}

.topOpponents {
  margin-top: 10px;
}

.topOpponent {
  margin: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-size: 14px;
}

.topWinningOpponentsTitle, .topLoosingOpponentsTitle {
  font-size: 18px;
  border-bottom: 2px solid #000;
  letter-spacing: 1.5px;
}

.tournamentSection {
  border: 2px solid #000;
  border-radius: 4px;
  width: 350px;
  height: 100%;
  margin-bottom: 20px;
  text-align: center;
}

.tournamentTitle {
  font-weight: bold;
  font-size: 18px;
  padding: 15px 15px 0px 15px;
  cursor: default;
}

.trendLineContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

.trendLineKeys {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.trendLineKey {
  font-size: 10px;
}

.winsLosses {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 3px;
  font-weight: bold;
  letter-spacing: 1px;
  cursor: default;
}

.wins {
  margin-right: 5px;
}

.losses {
  margin-left: 5px;
}

.winsLosses.kachi-koshi-tournament, .kachi-koshi {
  color: #33a02c;
}

.winsLosses.machi-koshi-tournament, .machi-koshi {
  color: saddlebrown;
}

.kachi-koshi, .machi-koshi {
  letter-spacing: 2px;
  font-weight: bold;
  margin-top: 3px;
  cursor: default;
}

.matchResult, .dayName {
  font-size: 12px;
}

.dayName {
  font-weight: 700;
  margin-right: 10px;
}
.dayResultRow {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start; 
  margin: 0 0 10px 10px;
}

.wrestlerMatchupsSection {
  text-align: center;
}

.wrestlerMatchups {
  border: 2px solid #000;
  border-radius: 4px;
  width: 400px;
  margin: 15px auto;
}

.wrestlerMatchupsHeader {
  font-size: 24px; 
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.matchupsFilterHelpText {
  margin-top: 10px;
}

.matchupsFilterInput {
  border: 2px solid #ccc;
  border-radius: 4px;
  padding: 5px;
  font-size: 16px;
  margin-top: 10px;
  width: 50%;
}

.matchupsEntry {
  padding: 15px 0 15px 0;
  border-bottom: 2px solid #000;
}

.matchupsEntry:last-child {
  border: none;
}

.matchupsTotalRow {
  font-weight: 700;
}

.matchupsTotalWins, .matchupsTotalLosses, .matchupsTotalName {
  margin-bottom: 5px;
}

.matchupsEntry table {
  margin: auto;
  font-size: 12px;
}

td, th {
  font-weight: normal;
  text-align: left;
  padding: 5px;
}

th {
  text-decoration: underline;
}

#wrestlerDetailHeader {
  
}

.wrestlerDetailHeaderInfo {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.wrestlerDetailHeaderGraphs {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin-top: 15px;
  height: 90px;
  overflow-y: scroll;
}

.wrestlerDetailHeaderGraphAndLabel {
  text-align: center;
}

.wrestlerDetailHeaderGraphLabel {
  font-size: 12px;
}

.wrestlerNameAndPhoto {
  width: 200px;
  text-align: center;
}

.wrestlerEnglishName {
  font-size: 24px;
  font-weight: 700;
}

.wrestlerJapaneseName {
  height: 47px;
  width: 100%;
  margin: auto;
  display: none;
}

.wrestlerJapaneseNameImgContainer {
  height: 100%;
  overflow: hidden;
  background-repeat: no-repeat;
  background-position: inherit;
  background-size: 175px auto;
}

.wrestlerPhoto {
  border: 2px solid #000;
  border-radius: 90px;
}

.wrestlerDetailBody {
  display: -webkit-flex;
  display: flex;
  margin: 30px;
}

.tournamentResultsSection {
  height: 100%;
  text-align: center;
}

.tournamentResultsTitle {
  font-size: 24px; 
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.tournamentResults {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 20px;
  height: 100%;
}

